import { checkPermission, showError } from '../../../actions/collaboratorActions';
import { FontAwesomeIcon } from '../../../Assets/AssetsHelper';
import { isWebsiteChanged } from '../../../utils/checkForNavigation';
import { Badge } from 'react-bootstrap';

const MenuTextIcon = ({ icon, text }) => <>
    <span className="menu-icon me-2 me-lg-0 mb-0 mb-lg-2">
        <FontAwesomeIcon icon={icon} />
    </span>
    <span className="menu-text">{text}</span>
</>

const AsideMenu = ({ authorized, navigate }) => {
    const authData = authorized?.userData?.user_data?.sessionData
    const planId = authorized?.userData?.user_data?.UserPlan?.planId
    const websiteUrl = authData?.createFrom.toLowerCase() == 'shopify' ? `shopify` : authData?.createFrom.toLowerCase() == 'bigcommerce' ? `bigcommerce` : 'publish'
    const navItemClass = `nav-menu-item f-center flex-lg-column`
    return <div className="sidebar-menu">
        <ul className="sidebar-menu-list list-unstyled mb-0 p-0" aria-label="Dashboard sidebar">
            <li className="sidebar-menu-items">
                <span className={`${navItemClass} ${window.location.pathname.includes('/home') ? 'active' : ''}`} onClick={() => isWebsiteChanged(() => navigate('/home'))}>
                    <MenuTextIcon icon="home" text="Home" />
                </span>
            </li>
            <li className="sidebar-menu-items">
                <span className={`${navItemClass} ${window.location.pathname.includes('/managefeed') || window.location.pathname.includes('/addfeed') ? 'active' : ''}`} onClick={() => checkPermission('networkManager', 'manageFeed') ? isWebsiteChanged(() => navigate(`/content/managefeed/${localStorage.getItem('wallId') ? localStorage.getItem('wallId') : ''}`)) : {}}>
                    <MenuTextIcon icon="grid-2-plus" text="Feeds" />
                </span>
            </li>
            <li className="sidebar-menu-items">
                <span className={`${navItemClass} ${window.location.pathname.includes('/moderation') ? 'active' : ''}`} onClick={() => isWebsiteChanged(() => navigate(`/content/moderation/${localStorage.getItem('wallId') ? localStorage.getItem('wallId') : ''}`))}>
                    <MenuTextIcon icon="image" text="Content" />
                </span>
            </li>
            <li className="sidebar-menu-items">
                <span id='pd_ctlog' className={`${navItemClass} ${window.location.pathname.includes('/products') ? 'active' : ''}`} onClick={() => checkPermission('product_catalog') ? isWebsiteChanged(() => navigate(`/content/products`)) : {}}>
                    <MenuTextIcon icon="bag-shopping" text="Product Catalog" />
                </span>
            </li>
            <li className="sidebar-menu-items">
                <span className={`${navItemClass} ${window.location.pathname.includes('/content/reviewhub') ? 'active' : ''}`} onClick={() => checkPermission('networkManager', 'manageFeed', true) ? isWebsiteChanged(() => navigate(`/content/reviewhub/${localStorage.getItem('wallId')}/29`)) : {}}>
                    <MenuTextIcon icon="star" text="Review Hub" />
                </span>
                <Badge bg="" className='f-center position-absolute badge-primary p-1 fs-10 border border-2 border-light badge m-0' style={{ top: -5, right: 4, padding: '1px 4px' }}>New</Badge>
            </li>
            <li className="sidebar-menu-items">
                <span id='publish' className={`${navItemClass} ${/\/(website|email-campaign|shopon-bio)/.test(window.location.pathname) ? 'active' : ''}`} onClick={() => {
                    if (
                        checkPermission('publish', 'email_camp', false, true) ||
                        checkPermission('publish', 'shopon', false, true) ||
                        checkPermission('publish', 'website', false, true)
                    ) {
                        isWebsiteChanged(() => {
                            if (checkPermission('publish', 'website', false, true)) {
                                navigate(`/website/${websiteUrl}${localStorage.getItem('website_id') ? `/${localStorage.getItem('website_id')}` : ''}`);
                            } else if (checkPermission('publish', 'shopon', false, true)) {
                                navigate(`/shopon-bio/preview/${localStorage.getItem('shopOn_id') || ''}`);
                            } else if (checkPermission('publish', 'email_camp', false, true)) {
                                navigate(`/email-campaign/publish/${localStorage.getItem('emailCamp_id') || ''}`);
                            }
                        });
                    }
                    else {
                        showError()
                    }
                }}>
                    <MenuTextIcon icon="paper-plane" text="Publish" />
                </span>
            </li>
            <li className="sidebar-menu-items">
                <span id='auto_pilot' className={`${navItemClass} ${window.location.pathname.includes('/autopilot') ? 'active' : ''}`} onClick={() =>    isWebsiteChanged(() => navigate(`/autopilot`))}>
                    <MenuTextIcon icon="toggle-on" text="AutoPilot" />
                </span>
                {planId == 57 ? <span className='f-center position-absolute bg-warning rounded-2' style={{ top: 0, right: 20, padding: '3px 4px', }}>
                    <FontAwesomeIcon icon="lock-keyhole" iconClass="text-light me-0 fs-10" />
                </span> : 
                <Badge bg="" className='f-center position-absolute badge-primary p-1 fs-10 border border-2 border-light badge m-0' style={{ top: -5, right: 4, padding: '1px 4px' }}>New</Badge>}
            </li>
            <li className="sidebar-menu-items">
                <span className={`${navItemClass} ${window.location.pathname.includes('/more-apps') ? 'active' : ''}`} onClick={() =>  checkPermission('otherSettings', 'apps_more') ?  isWebsiteChanged(() => navigate(`/more-apps/${localStorage.getItem('wallId') ? localStorage.getItem('wallId') : ''}`)) : {}}>
                    <MenuTextIcon icon="grid-2" text="Apps & more" />
                </span>
            </li>
            {/* <hr className="w-75 d-lg-flex d-none text-gray-200" style={{ margin: '1vh auto' }} /> */}
            <li><hr className="w-75 d-lg-flex d-none text-gray-200" style={{ margin: '1vh auto' }} /></li>
            <li className="sidebar-menu-items">
                <span className={`${navItemClass} ${window.location.pathname.includes('/creators') ? 'active' : ''}`} onClick={() => isWebsiteChanged(() => navigate(`/creators`))}>
                    <MenuTextIcon icon="user-group" text="Creators" />
                </span>
            </li>
            <li className="sidebar-menu-items">
                <span className={`${navItemClass} ${window.location.pathname.includes('/analytics') ? 'active' : ''}`} onClick={() =>  checkPermission('otherSettings', 'analytics') ? isWebsiteChanged(() => navigate(`/analytics`)) : {}}>
                    <MenuTextIcon icon="chart-mixed" text="Analytics" />
                </span>
            </li>
        </ul>
    </div>
}
export default AsideMenu;